import axios from "../config/axios";

const http = (config) => {
  return new Promise((resolve, reject) => {
    axios(config)
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data)
      })
  })
};

export const getListBlogs = () => {
  return http({
    method: 'get',
    url: '/Blogs',
    params: {
      limit: 10,
      order: '-updatedAt'
    }
  });
};

export const getBlogDetail = (objectId) => {
  return http({
    method: 'get',
    url: '/Blogs/' + objectId
  });
};



