<template>
  <div class="container">
    <div class="header"></div>
    <div class="body">
      <div v-for="item in lists" :key="item.id" style="margin: 20px;">
        <router-link :to="'/blog/p/' + item.id">
          <a-card
            hoverable
            style="width: 240px"
          >
            <a-card-meta
              :title="item.title">
              <div slot="description">{{item.subTitle}}</div>
            </a-card-meta>
          </a-card>
        </router-link>
      </div>
    </div>
    <div class="footer"></div>
  </div>
</template>

<script>
  import {getListBlogs} from 'api';
  import Vue from 'vue'
  import {Card} from 'ant-design-vue'

  Vue.use(Card);
  export default {
    name: "index",
    data() {
      return {
        lists: []
      }
    },
    mounted() {
      this.getData();
    },
    methods: {
      getData() {
        const me = this;
        getListBlogs().then(res => {
          me.parseData(res);
        })
      },
      parseData(res) {
        const me = this;
        res.results.forEach(data => {
          me.lists.push({
            title: data.title,
            subTitle: data.subTitle,
            id: data.objectId
          })
        })
      }
    }
  }
</script>

<style scoped>

</style>



