import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/view/index'
import Detail from '@/view/detail'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/blog/',
      name: 'index',
      component: Index
    },
    {
      path: '/blog/p/:id',
      name: 'detail',
      component: Detail
    },
  ]
})



