<template>
  <div class="container">
    <div class="header">
      <h2>{{article.title}}</h2>
      <h4>{{article.subTitle}}</h4>
    </div>
    <div class="body">
      {{article.content}}
    </div>
    <div class="footer"></div>

  </div>
</template>

<script>
  import {getBlogDetail} from 'api';

  export default {
    name: "detail",
    data() {
      return {
        id: null,
        article: {
          title: null,
          subTitle: null,
          content: null
        }
      }
    },
    mounted() {
      if (this.$route.params.id) {
        this.id = this.$route.params.id;
        this.getData();
      }
    },
    methods: {
      getData() {
        const me = this;
        getBlogDetail(me.id).then(res => me.parseData(res));
      },
      parseData(data) {
        this.article.title = data.title;
        this.article.subTitle = data.subTitle;
        this.article.content = data.content;
      }
    }
  }
</script>

<style scoped>
  .container {
    margin: 60px;
    text-align: center;
  }
</style>



