import axios from 'axios'
import LearnCloud from './LearnCloud'


axios.defaults.baseURL = 'https://leancloud.cn/1.1/classes';
axios.defaults.headers.common['X-LC-Id'] = LearnCloud.APP_ID;
axios.defaults.headers.common['X-LC-Sign'] = LearnCloud.sign;
// axios.defaults.headers.post['content-Type'] = 'appliction/x-www-form-urlencoded';

export default axios;



